/******Varibles******/
.hoverDiv:nth-child(2n) {
  background: #DCE8E0; }

.hoverDiv {
  background: #E0EDE3; }

.spanBlock {
  display: block;
  font-size: 14px; }

.accordianCardBG {
  background: #FBF8EF; }

.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 15px;
  border-radius: 5px;
  background: #33535C;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  -webkit-transition: opacity .2s;
  -o-transition: opacity .2s;
  transition: opacity .2s; }

.slider:hover {
  opacity: 1; }

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 1px solid #1D4042;
  background: #FFFFFF;
  cursor: pointer; }

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #F9F7EF;
  cursor: pointer; }

.btn-outline-status {
  color: #FF9999;
  background-color: transparent;
  background-image: none;
  border-color: #FF9999; }

.btn-outline-status.disabled, .btn-outline-status:disabled {
  color: #FF9999;
  background-color: transparent; }

.modal-body {
  word-wrap: break-word; }

.availability h2 {
  padding-top: 60px;
  font-size: 30px;
  line-height: 22px; }

.availability .subtitle {
  padding-top: 19px; }

.availability #tabs {
  margin-top: 20px;
  padding-bottom: 12px;
  border-bottom: 3px solid #33535C; }

.availability .radio-container {
  border-width: thin;
  background-color: #F2E6D0;
  border: none;
  margin-left: 10px;
  margin-right: 10px; }

.availability h3 {
  font-size: 15px;
  line-height: 22px;
  text-align: center; }

.availability .specific .time-input {
  border: none;
  border-bottom: 2px solid #32535B;
  background-color: inherit;
  color: #FF9999;
  text-align: center;
  margin: 0 auto; }

.availability .delete {
  height: 30px;
  width: 30px;
  padding: 2px;
  margin: 0px;
  margin-left: 5px;
  border: none;
  background-color: inherit; }

.availability .plus {
  height: 30px;
  padding: 2px;
  margin: 0px;
  border: none;
  background-color: inherit;
  font-size: 15px; }

.availability .day {
  text-align: center;
  font-size: 18px;
  line-height: 22px; }

.availability .submit {
  width: 124px;
  border-radius: 25px;
  background-color: #FF9999;
  color: white; }

.availability .submit:hover {
  background-color: #33535C;
  color: white; }

.availability .clear-all {
  width: 90px;
  border-radius: 25px;
  background-color: #33535C;
  color: white; }

.availability table {
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto; }

.availability th {
  margin-bottom: 10px;
  min-width: 112px; }

.availability .table-day {
  text-align: left; }

.availability .form-group input, .availability .form-group label {
  padding: 0 0;
  margin: 0 0; }

.availability .form-group, .availability table {
  width: 90%; }

.form-control1 {
  width: 196px; }



